.container {
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: 10%;
    margin-bottom: 10%;
    border: solid 2px;
    background-color: aliceblue;
    border-radius: 2%;
}
@media screen and (max-width: 300px) {
    .container {
        margin: 10%;
    }
}
@media screen and (min-width: 767px) and (min-height: 801px) {
    .container {
        transform: scale(1.5);
        margin-top: 100%;
    }
}
